}
.background {
	background-color: rgb(14, 18, 38);
	background-image: -moz-radial-gradient(200px 66%, circle, rgba(238,238,238,.2) 1, rgba(238,238,238,0) 50);
	overflow: hidden;
}
#cal {
  color: #fff; }

.themes ul {
  margin: 50px auto;
  padding-left: 0;
  display: flex;
  justify-content: space-around;
  width: 50%; }
  .themes ul li {
    list-style: none;
    flex: 1 1;
    margin: 0 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 1.5rem;
    border: 1px solid #ccc;
    cursor: pointer; }

.grid-container {
  display: grid;
  height: 450px;
  grid-template-columns: 1fr 400px 1fr;
  grid-template-rows: .5fr auto 1fr;
  grid-template-areas: "top top top" "left content right" "bottom bottom bottom";
  overflow: hidden; }

.top {
  grid-area: top; }

.left {
  grid-area: left; }

.content {
  grid-area: content; }

.bottom {
  grid-area: bottom; }

.right {
  grid-area: right; }

.cal {
  display: grid;
  grid-template-columns: repeat(7, 1fr); }
  .cal .day {
    position: relative;
    text-align: center;
    font-size: .75rem;
    padding: .3rem 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-width: 1px;
    border-style: solid;
    cursor: pointer; }
    .cal .day:after {
      content: '';
      position: absolute;
      top: 2px;
      right: 2px;
      bottom: 2px;
      left: 2px;
      z-index: -1;
      outline: solid transparent 2px; }
    .cal .day:hover:after {
      outline-color: rgba(238, 238, 238, 0.4); }
    .cal .day:active {
      background-color: rgba(238, 238, 238, 0.2); }

.foo {
  color: rgba(238, 238, 238, 0.3); }

.content {
  position: relative;
  z-index: 10; }

.mask {
  position: relative;
  z-index: 10; }

.theme-default {
  background-color: #292c33; }
  .theme-default .day {
    border-color: #292c33; }
    .theme-default .day:after {
      background: #292c33; }
  .theme-default .mask {
    background: #292c33; }

.theme-2 {
